.modalAnalyze {
  width: 800px;
  margin: 200px auto 0 auto;
  padding: 40px 35px;
  border-radius: 20px;
  background: linear-gradient(145deg, #101010, #1c1c1c);
  color: #F1F1F1;
  box-shadow: 0 0 30px rgba(0, 251, 255, 0.3);
  position: relative;
  overflow: hidden;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  z-index: 1;
}

/* –§–æ–Ω–æ–≤—ã–π —É–∑–æ—Ä (–∫–∞–∫ —É advantagesblock) */
.modalAnalyze::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(circle at 15% 25%, rgba(0,255,148,0.05) 0px, transparent 120px),
    radial-gradient(circle at 85% 70%, rgba(0,191,255,0.05) 0px, transparent 150px),
    repeating-linear-gradient(
      45deg,
      rgba(255, 255, 255, 0.04) 0px,
      rgba(0, 191, 255, 0.04) 1px,
      transparent 1px,
      transparent 32px
    );
  animation: floatPattern 25s linear infinite;
  z-index: 0;
}

/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ –∫—Ä—É–≥–∏ –ø–æ–≤–µ—Ä—Ö —É–∑–æ—Ä–∞, –Ω–æ –ø–æ–¥ –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º */
.modalAnalyze .circle {
  position: absolute;
  border-radius: 50%;
  opacity: 0.08;
  z-index: 0;
}

/* –ü–µ—Ä–≤—ã–π –∫—Ä—É–≥ */
.modalAnalyze .circle1 {
  width: 180px;
  height: 180px;
  background: rgba(0, 255, 148, 0.15);
  top: -60px;
  left: -60px;
  animation: floatCircle1 28s linear infinite;
}

/* –í—Ç–æ—Ä–æ–π –∫—Ä—É–≥ */
.modalAnalyze .circle2 {
  width: 140px;
  height: 140px;
  background: rgba(0, 191, 255, 0.15);
  bottom: -60px;
  right: -60px;
  animation: floatCircle2 32s linear infinite;
}

/* –¢—Ä–µ—Ç–∏–π –∫—Ä—É–≥ */
.modalAnalyze .circle3 {
  width: 100px;
  height: 100px;
  background: rgba(0, 255, 148, 0.1);
  top: 45%;
  left: 25%;
  animation: floatCircle3 36s linear infinite;
}

/* –ê–Ω–∏–º–∞—Ü–∏–∏ –∫—Ä—É–≥–æ–≤ */
@keyframes floatCircle1 {
  0% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(380px, 280px) scale(1.2); }
  100% { transform: translate(0, 0) scale(1); }
}

@keyframes floatCircle2 {
  0% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(-280px, -220px) scale(1.1); }
  100% { transform: translate(0, 0) scale(1); }
}

@keyframes floatCircle3 {
  0% { transform: translate(0, 0) scale(1); }
  50% { transform: translate(220px, -240px) scale(1.15); }
  100% { transform: translate(0, 0) scale(1); }
}

@keyframes floatPattern {
  from { background-position: 0 0, 0 0, 0 0; }
  to { background-position: 220px 220px, -220px -220px, 320px 320px; }
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.modalAnalyze h1 {
  text-align: center;
  color: #00FF94;
  text-shadow: 0 0 12px rgba(0, 255, 148, 0.6);
  margin-bottom: 35px;
  position: relative;
  z-index: 2;
}

/* –ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏–æ–Ω–Ω—ã–π –±–ª–æ–∫ */
.infoTakeAnalyze {
  background: linear-gradient(145deg, #1E1E1E, #1C1C1C);
  padding: 25px 30px;
  border-radius: 14px;
  margin-bottom: 40px;
  box-shadow: inset 0 0 15px rgba(0, 191, 255, 0.1);
  position: relative;
  z-index: 2;
}

.infoTakeAnalyze h2 {
  color: #00BFFF;
  margin-bottom: 15px;
  font-size: 20px;
  z-index: 2;
}

.infoTakeAnalyze p,
.infoTakeAnalyze li {
  font-size: 15px;
  line-height: 1.6;
  color: #B0BEC5;
  z-index: 2;
}

.infoTakeAnalyze li {
  list-style: none;
  margin: 6px 0;
  position: relative;
  padding-left: 18px;
  z-index: 2;
}

.infoTakeAnalyze li::before {
  content: "‚ú¶";
  color: #00BFFF;
  position: absolute;
  left: 0;
  z-index: 2;
}

/* –ò–Ω—Ç–µ—Ä–∞–∫—Ç–∏–≤–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã */
.interactiveItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;
}

.interactiveItem h3 {
  color: #00BFFF;
  margin: 20px 0 8px 0;
  align-self: flex-start;
}

/* –ò–Ω–ø—É—Ç—ã */
.selectMatch,
.interactiveItem textarea {
  width: 100%;
  max-width: 380px;
  padding: 12px 15px;
  border-radius: 10px;
  border: none;
  background: #1E1E1E;
  color: #F1F1F1;
  font-size: 1rem;
  margin-bottom: 20px;
  transition: box-shadow 0.3s ease, transform 0.2s ease;
  resize: none;
  max-height: 260px;
  overflow-y: auto;
  z-index: 2;
}

.selectMatch:focus,
.interactiveItem textarea:focus {
  outline: none;
  box-shadow: 0 0 12px rgba(0, 255, 148, 0.6), 0 0 8px rgba(0, 191, 255, 0.4);
  transform: translateY(-1px);
}

/* –ö–Ω–æ–ø–∫–∞ */
.submitMatch {
  margin-top: 10px;
  padding: 12px 28px;
  border-radius: 12px;
  border: none;
  font-size: 1rem;
  background: linear-gradient(135deg, #00FF94, #00BFFF);
  color: #121212;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  z-index: 2;
}

.submitMatch:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 18px rgba(0, 255, 148, 0.5), 0 0 18px rgba(0, 191, 255, 0.5);
}

/* –ê–¥–∞–ø—Ç–∏–≤ */
@media (max-width: 900px) {
  .modalAnalyze {
    width: 90%;
    padding: 30px 20px;
  }

  .selectMatch,
  .interactiveItem textarea {
    max-width: 100%;
  }
}